@import 'reset.css';
@import url('https://fonts.googleapis.com/css2?family=Anek+Malayalam:wght@300&display=swap');

* {
  box-sizing: border-box;
}

body {
  font-family: 'Anek Malayalam', sans-serif;
}

.title {
  height: 100vh;
  font-size: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #FFE6E6;
}

.scene-box {
  width: 70%;
  height: 140vh;
  font-size: 32px;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.first-scene {
  font-size: 60px;
  div {
    margin-bottom: 40px;
  }
}

.gif-container {
  text-align: center;
}

.second-scene {
  font-size: 60px;
  div {
    margin-bottom: 40px;
  }
}

.third-scene {
  font-size: 60px;
  div {
    margin-bottom: 40px;
  }
}

.fourth-scene {
  margin-top: 100px;
  font-size: 60px;
  div {
    margin-bottom: 40px;
  }
}

.left-msg {
  text-align: left;
  margin-bottom: 70px;
}

.right-msg {
  text-align: right;
  margin-bottom: 70px;
}

.fade-out-box {
  display: inline-block;
  padding: 10px;
  animation: fadeout 6s;
  animation-delay: 2s;
  -moz-animation: fadeout 6s; /* Firefox */
  -webkit-animation: fadeout 6s; /* Safari and Chrome */
  -o-animation: fadeout 6s; /* Opera */
  animation-fill-mode: forwards;
}
@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-moz-keyframes fadeout { /* Firefox */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-webkit-keyframes fadeout { /* Safari and Chrome */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
@-o-keyframes fadeout { /* Opera */
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.summer {
  color: #ffeb3b;
  font-weight: 600;
  -webkit-box-reflect: below -10px linear-gradient
          (transparent, #0008);
  text-shadow: 0 0 10px #ff8c3b,
  0 0 40px #ff8c3b,
  0 0 80px #ff8c3b,
  0 0 160px #ff8c3b,
  0 0 320px #ff8c3b;
}

.language {
  background: linear-gradient(to right, #f00 50%, #00f 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.walk-alone {
  width: 100%;
  height: 30%;
  margin-top: 10px;
  margin-bottom: 20px;
  object-fit: cover;
  clip-path: circle(35%);
}

.letter-container {
  width: 100%;
  height: 70vh;
  background-image: linear-gradient(to right, #000000, #453f96);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #fff;
  scroll-snap-type: y mandatory;
}

.center {
  margin-top: 2.5em;
  text-align: center;
}

.next-page-button {
  font-size: 1.5em;
  margin-top: 6em;
  padding: 8px 16px;
  text-decoration: none;
  background-color: darkgrey;
  border-radius: 50%;
}
